<template>
    <div id="widget">
        <header>
            <!-- Twitch Logo -->
            <svg height="32px" version="1.1" viewBox="0 0 94 32" width="94px" x="0px" y="0px">
                <path clip-rule="evenodd"
                      d="M88,5h-6V0h-9l-6,5h-5.5L59,7.5V5h-5V0H36v5H16l-5-5H0v22l5,5.25L14,32h6v-1.5l3,1.5h12l2-3l1,3h7v-3l3,3h8l0.5-3l2.5,3h10l3-3v3h4l3-3v3h7l7-6V10L88,5z M13,13H8v6h5v6H6l-4-4V2h6v5h5V13z M36,21.5L32.5,25H15V7h6v12h2V7h6v12h2V7h5V21.5z M44,25h-6V7h6V25z M44,5h-6V2h6V5z M57,13h-5v6h5v6h-7l-4-4V2h6v5h5V13z M72,13h-7v6h7v6h-9l-4-4V11l4-4h9V13z M91,25h-6V13h-5v12h-6V2h6v5h7l4,4V25z"
                      fill-rule="evenodd"></path>
            </svg>
        </header>
        <!-- Add Channel -->
        <div class="channel_add">
            <input type="text" placeholder="Channel Name" v-model="channelName" @keydown.enter="addChannel">
            <button  @click="addChannel">Add</button>
        </div>
        <!-- Show online channels -->
        <div class="channels">
            <h2>Online</h2>
            <!-- Channel -->
            <section class="channel" v-for="(channel, key, index) in channels" v-bind:key="channel._id"
            v-on:dblclick="deleteChannel(channel._id)">
                <div class="channel_main">
                    <!-- Channel: Info (Name & Game) -->
                    <div class="channel_info">
                        <h3 class="channel_name" v-text="channel.channel"></h3>
                        <p class="channel_game">Playing <strong>{{ channel.createdAt }}</strong></p>
                    </div>
                    <!-- Channel: Extra (Online Status) -->
                    <div class="channel_extra">
                        <span class="channel_presence online"></span>
                    </div>
                </div>
                <!-- Stream -->

            </section>
        </div>
        <!-- Show offline channels -->
        <div class="channels">
            <h2 class="channels_header">Offline</h2>            <!-- Channel -->

        </div>
    </div>
</template>

<script>
    import ChannelService from "../ChannelService";
    export default {
        name: 'ChannelComponent',
        data() {
            return {
                channels: [],
                error: '',
                channelName: '',
                status: true
            }
        },
        props: {
            msg: String
        },
        async created() {
            try {
                this.channels = await ChannelService.getChannels();
                console.log(this.channels);
            } catch (e) {
                this.error = e.message;
            }
        },
        methods: {
            async addChannel() {
                try {
                    await ChannelService.createChannel(this.channelName);
                    this.channels = await ChannelService.getChannels();
                    this.channelName = '';
                } catch (e) {
                    this.error = e.message;
                }
            },
            async deleteChannel(id) {
                try {
                    await ChannelService.deleteChannel(id);
                    this.channels = await ChannelService.getChannels();
                    this.channelName = '';
                } catch (e) {
                    this.error = e.message;
                }
            }
        },

    }
</script>
